import React, { memo } from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import { ControlStyle } from './types';

import styles from './styles.scss';

const cd = cssDecorator(styles);

interface ControllButtonProps {
  /** Цветовая схема кнопки */
  colorStyle?: ControlStyle;

  /** Кнопка заблокирована? */
  disabled?: boolean;

  /** Дочерние элементы */
  children?: React.ReactNode;

  /** ID для тестирования */
  dataTestId?: string;

  /** Обработчик нажатия на кнопку */
  onClick?: VoidFunction;
}

/** Кнопка из панели управления */
function ControllButton({
  colorStyle = 'default',
  disabled,
  children,
  dataTestId,
  onClick
}: ControllButtonProps): JSX.Element {
  return (
    <button
      className={ cd('action', { [colorStyle]: true }) }
      type="button"
      disabled={ disabled }
      onClick={ onClick }
      data-test-id={ dataTestId }
    >
      { children }
    </button>
  );
}

const MemoControllButton = memo(ControllButton);
MemoControllButton.displayName = 'ControllButton';

export default MemoControllButton;
